const state = { //
    direction: '',
    active: false,
    isLoading: false,
    token: "",
    text: "",
    show: false,
    userInfo: {},
    bottomLoding: false,
    code: 0,
    verify_code: '',
}

// getters
const getters = {
    direction: state => state.direction,
    verify_code: state => state.verify_code,
    token: state => state.token,
    active: state => state.active,
    show: state => state.show,
    text: state => state.text,
    isLoading: state => state.isLoading,
    userInfo: state => state.userInfo,
    bottomLoding: state => state.bottomLoding,
    code: state => state.code
}

const actions = {
        SET_TOKEN({ commit }, { token }) {
            commit('SET_TOKEN', token)
        },
        SET_ACTIVE({ commit }, { active }) {
            commit('SET_ACTIVE', active)
        },
        SET_SHOW({ commit }, { show }) {
            commit('SET_SHOW', show)
        },
        SET_TEXT({ commit }, { text }) {
            commit('SET_TEXT', text)
        },
        SET_USERINFO({ commit }, { userInfo }) {
            commit('SET_USERINFO', userInfo)
        },
        SET_BOTTOMLOADING({ commit }, { bottomLoding }) {
            commit('SET_BOTTOMLOADING', bottomLoding)
        },
        SET_CODE({ commit }, { verify_code }) {
            commit('SET_CODE', verify_code)
        },
        SET_ISLODING({ commit }, { isLoading }) {
            commit('SET_ISLODING', isLoading)
        }
    }
    // mutations
const mutations = {
    SET_CODE: (state, verify_code) => {
        state.verify_code = verify_code
    },
    SET_SHOW: (state, show) => {
        state.show = show
    },
    SET_TOKEN: (state, token) => {
        state.token = token
    },
    SET_ACTIVE: (state, active) => {
        state.active = active
    },
    SET_TEXT(state, text) {
        state.text = text
    },
    SET_USERINFO(state, userInfo) {
        state.userInfo = userInfo
    },
    updateDirectionStatus(state, payload) { //
        console.log(state)
        state.direction = payload.direction
    },
    updateLoadingStatus(state, payload) { //
        state.isLoading = payload.isLoading
    },
    SET_BOTTOMLOADING(state, bottomLoding) {
        state.bottomLoding = bottomLoding
    },
    SET_ISLODING(state, isLoading) {
        state.isLoading = isLoading
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


