import axios from 'axios'
import Vue from 'vue';
import { getToken } from '@/utils/auth'
import store from '../store'
import { URL } from '@/utils/config'
import { jsMd5, paQString } from '@/utils/jsmd5';
import { Toast } from 'mint-ui'

const service = axios.create({
    baseURL: URL, // apibase_url:process.env.BASE_API
    // baseURL: `/api`,
    responseType: 'json',
    headers: { "Content-Type": "application/x-www-form-urlencoded" },
    timeout: 200000 // 
})

service.interceptors.request.use(config => {
    // debugger
    let mdsign;
    console.log("prototype")
    console.dir(Vue.prototype.tokus)
    config.headers['token'] = Vue.prototype.tokus.token
    config.headers['version'] = Vue.prototype.tokus.version
    config.headers['device_no'] = Vue.prototype.tokus.device_no
    config.headers['app-type'] = Vue.prototype.tokus['app-type']
    console.dir(config)
    return config
}, error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
})

service.interceptors.response.use(
        response => {
            const res = response.data
            if (res.code !== 0) {
                Toast(res.message)
            }
            return response.data
        },
        error => {
            Toast(error.message);
        }
    )
