import request from '@/utils/request'
import Qs from 'qs'

// 
export function walletWithdraw(data) {
    return request({
        transformRequest: [function(data) {
            //  data 
            return Qs.stringify(data)
        }],
        url: '/api/app/merchant/wallet/withdraw/withdraw',
        method: 'post',
        data: data
    })
}

// 
export function getWithdrawInfo(data) {
    return request({
        url: '/api/app/merchant/wallet/withdraw/getWithdrawInfo',
        method: 'get',
        params: data
    })
}

// 
export function getVerifyCode(data) {
    return request({
        url: '/api/app/merchant/sms/getVerifyCode',
        method: 'get',
        params: data
    })
}

// 
export function checkVerifyCode(data) {
    return request({
        transformRequest: [function(data) {
            //  data 
            return Qs.stringify(data)
        }],
        headers: {
            'deviceCode': 'A95ZEF1-47B5-AC90BF3'
        },
        url: '/api/app/merchant/wallet/checkVerifyCode',
        method: 'post',
        data: data
    })
}

// 
export function changePassword(data) {
    return request({
        transformRequest: [function(data) {
            //  data 
            return Qs.stringify(data)
        }],
        headers: {
            'deviceCode': 'A95ZEF1-47B5-AC90BF3'
        },
        url: '/api/app/merchant/wallet/changePassword',
        method: 'post',
        data: data
    })
}


